import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  // {
  //   path: '/pricing',
  //   name: 'Pricing',
  //   component: () => import(/* webpackChunkName: "price" */ '../views/Pricing.vue')
  // },
  {
    path: '/docs',
    name: 'Document',
    component: () => import(/* webpackChunkName: "price" */ '../views/Document.vue')
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
  scrollBehavior() {
    // always scroll to top
    return { top: 0 }
  },
})

export default router
