//import Vue from "vue";
import { createApp } from 'vue/dist/vue.esm-bundler'
import App from './App.vue'
import router from './router'
import store from './store'
import "./scss/style.scss"




//import {getDate} from "./utils/date";
import {getNumber} from "./utils/number";



const lambda = createApp(App);
lambda.component("modal",{

    props:['name'],
    template:`
		<div class="modal  modal-active">
			<div class="modal-dialog">
					<div class="modal-content" style="width: 595px">
						<div class="modal-header">
							
							<h5 class="modal-title">
								{{name}}
							</h5>

                          <button type="button" @click="$emit('close')" class="btn-close"></button>
						</div>
						<div class="modal-body">
								<slot></slot>
						</div>
						
				    </div>
			</div>
		</div>`
})

lambda.use(store).use(router);
lambda.config.globalProperties.$filters = {
    number(value) {
        return getNumber(value)
    }
}
lambda.mount('#app');



