    tobii.on('previous', function (e) {
      if(e.detail.group === 'events')
        console.log('event: ' + 'previous', e.detail)
    })
    tobii.on('next', function (e) {
      if(e.detail.group === 'events')
        console.log('event: ' + 'next', e.detail)
    })
    tobii.on('close', function (e) {
      if(e.detail.group === 'events')
        console.log('event: ' + 'close', e.detail)
    })
  }

}
</script>
